import { Component, OnInit, Input, HostBinding } from '@angular/core';
import { FilterCondition } from '@farris/ui-common/types';

@Component({
    selector: 'filter-code',
    template: `
    <div style="overflow:auto" [style.height.px]="height - 38">
        <pre class="flex-fill">
            <code>{{ conditions | json }}</code>
        </pre>
    </div>
    `,
    styles: [
        `
        pre {
            color: #d8d9d9;
            background: #424242;
            padding-left: 20px;
            padding-top: 20px;
            margin-bottom: 0;
            font-size: 16px;
        }
        `
    ]
})

export class FilterCodeComponent implements OnInit {

    @HostBinding('class') cls = 'd-flex flex-column flex-fill';
    @Input() height: number;
    @Input() conditions: FilterCondition[] = [];

    constructor(private el: ElementRef) { }

    ngOnInit() {
        this.el.nativeElement.style.overflow = 'auto';
    }
}
