import { Component, OnInit, Input, HostBinding, SimpleChanges, OnChanges } from '@angular/core';
import { FilterCondition } from '@farris/ui-common/types';
import { FilterService } from './../services/filter.service';

@Component({
    selector: 'filter-sql',
    template: `
    <pre class="flex-fill" [style.height.px]="height - 38">
<code>{{ sql  }}</code>
    </pre>
    `,
    styles: [
        `
        pre {
            color: #d8d9d9;
            background: #424242;
            padding-left: 20px;
            padding-top: 20px;
            margin-bottom: 0;
            font-size: 16px;
        }
        `
    ]
})

export class FilterSqlCodeComponent implements OnInit, OnChanges {

    @Input() height: number;
    @HostBinding('class') cls = 'd-flex flex-column flex-fill';

    @Input() conditions: FilterCondition[] = [];

    sql = '';

    constructor(private filterService: FilterService) { }

    ngOnInit() {
        this.sql = this.filterService.buildSqlWhere(this.conditions);
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.conditions && !changes.conditions.isFirstChange()) {
            this.sql = this.filterService.buildSqlWhere(this.conditions);
        }
    }

}
