import { Injectable, Optional } from '@angular/core';
import { Subject } from 'rxjs';
import { FilterCondition, Compare, FilterRelation, CompareOperators } from '@farris/ui-common/types';
import { CommonUtils } from '@farris/ui-common';
import { LocaleService } from '@farris/ui-locale';
@Injectable()
export class FilterService {

    conditionsChanged = new Subject<FilterCondition[]>();
    commonUtils: CommonUtils;
    constructor(@Optional() utils: CommonUtils) {
        this.commonUtils = utils || new CommonUtils();
    }

    buildSqlWhere(c: any) {
        return this.commonUtils.buildSqlWhere(c);
    }


    isValid(conditions: FilterCondition[], localeService?: LocaleService) {
        if (conditions && conditions.length) {
            // æ¸é¤æåæ¡ä»¶çå³ç³»ç¬¦
            conditions[conditions.length - 1].relation = FilterRelation.Empty;

            // æ£æ¥å³ç³»è¿æ¥ç¬¦
            const emptyRelations = conditions
                .map((n: any, i) => {
                    if (n.relation == "" + FilterRelation.Empty) {
                        return { index: i, filter: n };
                    }
                    return null;
                })
                .filter((n, i) => {
                    return n && i !== conditions.length - 1;
                });

            let relationFlag = true;
            if (emptyRelations.length) {
                relationFlag = false;
            }

            // æ£æ¥æ¬å·
            let bracketFlag = true; 
            let bracketStr = '';
            for (let i = 0; i < conditions.length; i++) {
                bracketStr += conditions[i].lbracket + conditions[i].rbracket;
            }
           
            const l = bracketStr.length ;
            if (l % 2 > 0) {
                bracketFlag = false;
            } else {
                for(let i =0; i< l; i++ ){
                    bracketStr = bracketStr.replace('()', '');
                }
                bracketFlag = !bracketStr.length;
            }

            // æ£æ¥å­æ®µæ¯å¦è®¾ç½®
            const emptyFields = conditions.filter((n: any) => !n.filterField).length === 0;


            return {
                bracket: {
                    result: bracketFlag,
                    msg: localeService ? localeService.getValue('filterEditor.validate.bracket') : 'æ¬å·ä¸å¹éï¼è¯·æ£æ¥ã'
                },
                relation: {
                    result: relationFlag,
                    msg: localeService ?localeService.getValue('filterEditor.validate.relation') : 'å³ç³»è®¾ç½®ä¸å®æ´ï¼è¯·æ£æ¥ã'
                },
                field: {
                    result: emptyFields,
                    msg: localeService?localeService.getValue('filterEditor.validate.field'): 'å­æ®µä¸åè®¸ä¸ºç©ºï¼è¯·æ£æ¥ã'
                }
            };
        }

        return true;
    }
}
